.wrapper{
    display: grid;
    grid-template-columns: 2fr 4fr;
    grid-template-areas:
    "fd dw";
    grid-gap: 10px;
}
.friendDialogs{
    grid-area: fd;
    background-color: #f8f8f8;
}
.dialogWindow{
    grid-area: dw;
    background-color: #f8f8f8;
}
.dialogList{
    margin: 30px 15px;
}
.dialogName{
    margin: 15px 0px;
    transition: ease-out 0.1s;
    cursor: pointer;
    color: #000;
}
.dialogName a{
    color: #000;
}
.dialogName:hover{
    font-size: larger;
    transition: ease-out 0.1s;
    cursor: pointer;
}
.messages{
    margin-top: 40px;
}
.message{
    margin: 30px 10px;
}
.postMessages{
    display: flex;
}
.postMessages textarea{
    max-width: 350px;
    width: 100%;
    margin-left: 6px;
}
.postMessages button{
    max-width: 100px;
    width: 100%;
    height: 53px;
}
.active{
    font-size: larger;
    color: #000;
}